import { typings } from '~utils';
import { createMachine } from './machine';

export const DEFAULT_MACHINE = createMachine(
  {
    states: {
      on: {
        on: {
          SWITCH: {
            actions: 'inc',
            target: '/off',
          },
        },
      },
      off: {
        on: {
          SWITCH: {
            actions: 'dec',
            target: '/on',
          },
        },
      },
    },
  },
  typings({
    eventsMap: { SWITCH: 'primitive' },
    context: { iterator: 'number' },
  }),
  { '/': 'off' },
).provideOptions(({ assign }) => ({
  actions: {
    inc: assign(
      'context.iterator',
      ({ context: { iterator } }) => iterator + 1,
    ),
    dec: assign(
      'context.iterator',
      ({ context: { iterator } }) => iterator - 1,
    ),
  },
}));
